.headline {
    padding-top: 55px;
    font-size: 32px;
    text-align: center;
    color: rgb(111, 168, 129);
    font-weight: bold;
    letter-spacing: 3px;
    margin-top: 21px;
    margin-bottom: 21px;
}

.grid-container {
    position: static;
    display: grid;
    height: auto;
    width: 100%;
    grid-template-rows: min-content 1fr;
    grid-template-columns: 100%;
    padding-bottom: 0;
    box-sizing: border-box;
  }
  
.grid {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
}